$transitionTime: 0.5s;
$numberOfProjects: 5;

// $cols - number of column
// n - number of items

@mixin moveInGrid($cols, $n) {
    $rows: ceil($n / $cols);

    .grid {
        grid-template-columns: repeat($cols, 1fr);
        grid-template-rows: repeat($rows, 100% / $rows);

        li:last-child:after,
        li:last-child:before {
            left: 100% * (($cols - $n) % $cols);
            transform: translate3d(-100% * (($cols - 1) / 2), -100% * (($rows - 1) / 2), 0);
        }

        @for $i from 1 through $n - 1 {
            $x: ($cols - $i) % $cols;
            $y: $rows - floor(($i - 1) / $cols) - 1;

            li:nth-child(#{$i}):hover~li:last-child:after,
            li:nth-child(#{$i}):hover~li:last-child:before {
                transform: translate3d(-$x*100%, -$y*100%, 0);
            }
        }

        li:last-child:hover:after,
        li:last-child:hover:before {
            transform: translate3d(-100% * (($cols - $n) % $cols), 0%, 0);
        }
    }

}

@mixin colors($n, $colors) {
    @for $i from 1 through $n - 1 {
        li:nth-child(#{$i}):hover~li:last-child:after {
            @if (nth($colors, $i)) {
                background: nth($colors, $i % $n);
            }
        }
    }

    li:last-child:hover:after {
        background: nth($colors, -1);
    }
}

.title,
a {
    display: block;
    height: 100%;
}

.title {
    max-width: 100%;
    width: 100%;
    object-fit: cover;
    position: absolute;
    top: 50%;
    font-size: 25px;
    font-family: 'Rock Salt', cursive;
    @extend .unselecteable;
}

.grid {
    display: grid;
    overflow: hidden;
    list-style: none;
    margin: 0;
    gap: 10px;
    padding: 25px;
    width: 90vw;
    height: 90vh;

    li {
        text-align: center;
        position: relative;
        cursor: pointer;
    }

    li:hover~li:last-child:after,
    li:last-child:hover:after,
    li:hover~li:last-child:before,
    li:last-child:hover:before {
        opacity: 1;
        transition: $transitionTime ease;
    }

    li:last-child:after,
    li:last-child:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        pointer-events: none;
        clip-path: polygon(0 0, 0 100%, 100% 100%, 100% 0);
        -webkit-clip-path: polygon(0 0, 0 100%, 100% 100%, 100% 0);
        opacity: 0;
        transition: opacity $transitionTime, transform $transitionTime $transitionTime, background $transitionTime;
    }

    li:last-child:after {
        mix-blend-mode: multiply;
        background: #aaafc3;
    }

    li:last-child:before {
        backdrop-filter: grayscale(100%) blur(2px);
        -webkit-backdrop-filter: grayscale(100%) blur(2px);
    }

    //colors (optional)
    @include colors($numberOfProjects, [ #d9e826, #45abba, #bf4040, #cc00ff, #00ffff, #1c82ab, #80b280, #fff ]);
}

@include moveInGrid(3, $numberOfProjects);

@media (max-width: 60em) {
    @include moveInGrid(2, $numberOfProjects);
}

.description {
    font-family: "Unica One", sans-serif;
    font-size: 1.25rem;
    position: absolute;
    display: block;
    right: 2rem;
    left: 2rem;
    text-align: center;
    top: 50%;
    margin: auto;
    color: white;
    z-index: 100;
    opacity: 0;
    transform: translate3d(0, -50%, 0);
    transition: .3s 0s;
    @extend .unselecteable;
}

li:hover .description {
    opacity: 1;
    transition: .6s .3s;
}

li:hover .title {
    opacity: 0;
    transition: .6s .3s;
}